import React, { Component } from 'react'
import JsonData from '../Projects_Data.json'

const Title = (props) => {
    return (
        <div className="abouttitle">
            <h6>{props.titleh6}</h6>
            <h1>{props.titleh1}</h1>
        </div>
    )
}

const Block = (props) => {
    return (
        <div className="block">
            <h1>{props.h1}</h1>
            <div className="pTags">
                <p>{props.p1}</p>
                <p>{props.p2}</p>
            </div>
            <img className="blockImg" src={props.img} alt={props.h1} />
        </div>
    )
}

const Blocks = () => {
    return (
        <div className="blocks">
            <div className="container">
                <div className="blocksContainer">
                    {JsonData.map((item, index) => {
                        return (
                            <a href={item.link} target="_blank" key={index} >
                                <Block item={item}
                                    h1={item.name}
                                    p1={item.paragraph1}
                                    p2={item.paragraph2}
                                    img={item.image}
                                />
                            </a>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}


class Projects extends Component {
    render() {
        return (
            <div className="projects pages">
                <Title titleh6="Showcasing some of my best work" titleh1="Portfolio" />
                <Blocks />
            </div>
        )
    }
}

export default Projects
