import React, { Component } from 'react'
import heroImg from '../images/heroImg.jpeg'
import CV from './FaridGarayevCV.pdf'


const Title = (props) => {
    return (
        <div className="abouttitle">
            <h6>{props.titleh6}</h6>
            <h1>{props.titleh1}</h1>
        </div>
    )
}

const WhoAmI = () => {
    return (
        <div className="whoami">
            <div className="titleFirst">
                <div className="titleImg">
                    <img src={heroImg} alt="Me" />
                </div>
            </div>

            <div className="titleSec">
                <div className="titleMe">
                    <h3><span>Who am i?</span></h3>
                    <h2>My name is Farid Garayev.I'm a Front End Developer.</h2>
                    <p>
                        I'm 20 years old.I am studying at Baku Engineering University.
                        I can create a website as customer wish.
                        I describe myself as a passionate developer who loves coding, open source, and the web platform.
                        I use HTML, CSS, JavaScript and ReactJS to produce responsive websites and web apps that provide users
                        the best and most appropriate experience suited to their device and browser.
                    </p>
                </div>

                <div className="aboutMe">
                    <div className="row">
                        <div className="col-md-6">
                            <p>Name : <span>Farid Garayev</span></p>
                        </div>
                        <div className="col-md-6">
                            <p>Email : <span>farid.garayev.1@gmail.com</span></p>
                        </div>
                        <div className="col-md-6">
                            <p>Age : <span>20</span></p>
                        </div>
                        <div className="col-md-6">
                            <p>From : <span>Baku,Azerbaijan</span></p>
                        </div>
                    </div>
                </div>

                <div className="aboutSocial">
                    <div className="row">
                        <div className="col-md-6">
                            <button><a href={CV} download >Download CV <i className="far fa-check-circle"></i></a></button>
                        </div>
                        <div className="col-md-6">
                            <div className="socialIcons">
                                <a target="_blank" href="https://twitter.com/"><i className="fab fa-twitter"></i></a>
                                <a target="_blank" href="https://www.instagram.com/faridgaraevv/"><i className="fab fa-instagram"></i></a>
                                <a target="_blank" href="https://www.linkedin.com/in/farid-garayev-b14873178/"><i className="fab fa-linkedin-in"></i></a>
                                <a target="_blank" href="https://www.facebook.com/farid.garayev.39"><i className="fab fa-facebook-f"></i></a>
                                <a target="_blank" href="https://github.com/b1ackBug"><i className="fab fa-github"></i></a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

const MySkills = () => {
    return (
        <div className="skills">
            <div className="row">
                <div className="col-md-5 col-sm-12 my-4">
                    <h3>Technical Skills</h3>
                    <div className="skill">
                        <div className="skill-name">HTML</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={97} style={{ width: "97%" }} />
                        </div>
                    </div>
                    <div className="skill">
                        <div className="skill-name">CSS(SASS)</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={97} style={{ width: "97%" }} />
                        </div>
                    </div>
                    <div className="skill">
                        <div className="skill-name">Bootstrap</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={95} style={{ width: "95%" }} />
                        </div>
                    </div>
                    <div className="skill">
                        <div className="skill-name">javascript</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={80} style={{ width: "80%" }} />
                        </div>
                    </div>
                    <div className="skill">
                        <div className="skill-name">jQuery</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={80} style={{ width: "80%" }} />
                        </div>
                    </div>
                    <div className="skill">
                        <div className="skill-name">ReactJS</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={65} style={{ width: "65%" }} />
                        </div>
                    </div>
                </div>

                <div className="col-md-5 col-sm-12 my-4">
                    <h3>Professional Skills</h3>
                    <div className="skill">
                        <div className="skill-name">Communication</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={80} style={{ width: "80%" }} />
                        </div>
                    </div>
                    <div className="skill">
                        <div className="skill-name">Team Work</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={90} style={{ width: "90%" }} />
                        </div>
                    </div>
                    <div className="skill">
                        <div className="skill-name">Project Managment</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={90} style={{ width: "90%" }} />
                        </div>
                    </div>
                    <div className="skill">
                        <div className="skill-name">Creativlity</div>
                        <div className="skill-bar">
                            <div className="skill-per" per={85} style={{ width: "85%" }} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

class About extends Component {
    render() {
        return (
            <div className="about pages">
                <div className="container">
                    <Title titleh6="Get to know me" titleh1="About Me" />
                    <WhoAmI />
                    <div className="mySkills">
                        <Title titleh6="My level of knowledge in some tools" titleh1="My SKills" />
                        <MySkills />
                    </div>
                </div>
            </div>
        )
    }
}

export default About
